<template>
  <div>
    <div class="question-title">
      <span class="badge">必填</span>
      <span class="title">{{ title }}</span>
      <span class="description" v-if="description && description.replace(/\s/g, '')">（{{ description }}）</span>
    </div>
    <template v-if="showType === 'radio' || showType === 'judge'">
      <xRadioGroup class="option-group" v-model="select" vertical @on-change="changeOption">
        <xRadio v-for="(option, idx) in properties" :key="idx" :disabled="readonly || readonlyData.indexOf(idx) !== -1" :label="idx" @click.native="changeOptionSelect(idx)">
          <span class="option-title">{{option.title}}</span>
          <span class="option-description">{{option.description ? '（' + option.description + '）' : ''}}</span>
        </xRadio>
      </xRadioGroup>
    </template>
    <!-- <template v-else-if="showType === 'judge'">
      <div class="judge-button-group">
        <Button
          class="judge-button"
          v-for="(item, index) in properties"
          @click="changeOption(index)"
          size="large"
          :disabled="readonly || readonlyData.indexOf(index) !== -1"
          :class="select === index ? 'active' : ''"
          :key="index">
          {{item.title}} {{item.description ? '(' + item.description + ')' : ''}}
        </Button>
      </div>
    </template> -->
    <template v-else-if="showType === 'dropDown'">
      <Select v-model="select" @on-change="changeOption" filterable clearable :disabled="readonly" style="width:400px">
        <Option v-for="(item, idx) in properties" :value="idx" :disabled="readonlyData.indexOf(idx) !== -1" :key="idx">{{item.title}} {{item.description ? '(' + item.description + ')' : ''}}</Option>
      </Select>
    </template>
  </div>
</template>

<script>
export default {
  data () {
    return {
      select: '',
      uuid: '00000000-0000-0000-0000-000000000005'
    }
  },
  props: {
    schemaKey: {
      type: String,
      require: true
    },
    title: {
      type: String,
      default: '单选题目'
    },
    value: {
      type: String,
      default: ''
    },
    description: {
      type: String,
      default: ''
    },
    properties: {
      type: Object
    },
    additionalKeys: {
      type: Array
    },
    showType: {
      type: String,
      default: 'radio'
    },
    readonly: {
      type: Boolean
    },
    readonlyData: {
      type: Array,
      default: () => []
    },
    server: {
      type: Boolean,
      require: false
    }
  },
  watch: {
    value () {
      this.select = this.value
    }
  },
  mounted () {
    try {
      if (this.value) {
        this.select = JSON.parse(this.value)[0]
      }
    } catch (err) {
      console.log(err)
      this.select = null
    }
    console.log('追加题', this.additionalKeys)
  },
  methods: {
    changeOptionSelect (v) {
      if (this.select === v) {
        this.changeOption(v)
      }
    },
    changeOption (val) {
      this.select = val
      this.$emit('input', {
        schemaKey: this.schemaKey,
        value: [val]
        // server: this.properties[val].server !== undefined ? this.properties[val].server : false
      })

      if (this.properties[val].additionalKeys && this.properties[val].additionalKeys.length) {
        this.$emit('additional', {
          allowClose: false,
          additionalKeys: this.properties[val].additionalKeys
        })
      } else {
        if (this.properties[val].commit && this.properties[val].commit === true) {
          this.$emit('commit', {
            schemaKey: this.schemaKey,
            uuid: this.uuid
          })
        }
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.question-title
    text-align left
    margin-bottom 0.5rem
    .badge
      display inline-block
      color #ffffff
      background-color #f39938
      height 24px
      line-height 24px
      width 46px
      text-align center
      border-radius 12px
      font-size 13px
    .title
      font-size 14px
      font-weight bold
      margin-left 0.5rem
    .description
      font-size 13px
      color #666
      margin-left 0.5rem
.judge-button-group
  text-align center
  .judge-button
    width 10rem
    height 6rem
    margin-right 0.5rem
    background-image linear-gradient(90deg, #F7F7F7, #F7F7F7)
    white-space normal
    overflow-y hidden
  .active
    background-image linear-gradient(90deg, #EAEAEA, #EAEAEA)
.option-group
  text-align left
  .option-title
    font-weight bold
    font-size 15px
    font-family "微软雅黑"
  .option-description
    color #96A0AD
    font-size 14px
</style>
